<template>
  <div class="milestone-manage">
       <section style="border-radius:5px;padding:8px 0;background:#fff;box-shadow:0 0 5px #ccc">     
            <div class="filter">
              <span class="filter-title">: </span><el-input size="small" style="width:50%" placeholder="/"  suffix-icon="el-icon-search" v-model="keyword" @change="searchLcbContent"> </el-input>
              &#x3000;<el-button size="mini" type="primary" @click="handleSearchLcb"></el-button>
            </div>
            <div style="display:flex" class="filter">
                <span class="filter-title">:</span>
               <el-checkbox-group v-model="checkList" @change="handleCheckbox">
                    <el-checkbox label="1"></el-checkbox>
                    <el-checkbox label="2"></el-checkbox>
                    <el-checkbox label="3"></el-checkbox>
                    <el-checkbox label="4"></el-checkbox>
                    <el-checkbox label="5"></el-checkbox>
                    <el-checkbox label="6"></el-checkbox>
                    <el-checkbox label="7"></el-checkbox>
                    <el-checkbox label="9"></el-checkbox>
                </el-checkbox-group>
            </div>
            <div style="display:flex" class="filter">
                <span class="filter-title">:</span>
               <el-checkbox-group v-model="xmlbList" @change="handleXMlb">
                    <el-checkbox label=""></el-checkbox>
                    <el-checkbox label=""></el-checkbox>
                    <el-checkbox label=""></el-checkbox>
                    <el-checkbox label=""></el-checkbox>
                </el-checkbox-group>
            </div>
            <div class="filter">
                <span class="filter-title"></span>
                     <el-date-picker
                        v-model="cnkssj"
                        type="date"
                        size="small"
                        placeholder=""
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd"
                        @change="changeCnStartDate">
                     </el-date-picker>
                     &nbsp;&nbsp;
                     <el-date-picker
                        v-model="cnjssj"
                        type="date"
                         size="small"
                        placeholder=""
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd"
                        @change="changeCnEndDate">
                     </el-date-picker>
            </div>
            <div class="filter">
                <span class="filter-title"></span>
                 <el-date-picker
                        v-model="sjkssj"
                        type="date"
                         size="small"
                        placeholder=""
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd"
                        @change="changeSjStartDate">
                     </el-date-picker>
                      &nbsp;&nbsp;
                     <el-date-picker
                        v-model="sjjssj"
                        type="date"
                         size="small"
                        placeholder=""
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd"
                        @change="changeSjEndDate">
                    </el-date-picker>
            </div>
       </section>
       <section class="milestone_tabel">
           <div style="padding:0 10px 10px;">
                <p style="margin:10px 0 !important;"  v-if="ishow">
                   <el-button size="mini" type="success" @click="commitLcb"></el-button>
                   <el-button size="mini" type="primary" @click="exportLcb"></el-button>
                   <span style="float:right;margin-top:5px"><span class="filter-weight"> : </span><span style="color:#f00;font-size:18px">{{totalWgl<10000?totalWgl:totalWgl<100000000?(totalWgl/10000).toFixed(4):(totalWgl/100000000).toFixed(4)}} 
                   </span>{{totalWgl<10000?'':totalWgl<100000000?'':''}}</span>
                   <p style="color:#aaa;font-size:12px;"></p>
                </p>
                <el-table
                    ref="multipleTable"
                    :data="tableData3"
                    border
                    tooltip-effect="dark"
                    @selection-change="handleSelectionChange">
                    <el-table-column type="selection" max-width="80" :selectable='checkboxInit' v-if="ishow"></el-table-column>
                    <el-table-column prop="xmnr_display" min-width="160"  label=""  show-overflow-tooltip></el-table-column>
                    <el-table-column prop="lcbms_display" min-width="180" style="text-align:left" label="" show-overflow-tooltip> </el-table-column>
                    <el-table-column prop="nrxmlb" min-width="90"  label=""  show-overflow-tooltip></el-table-column>
                    <el-table-column sortable label="" width="120" show-overflow-tooltip> 
                         <template slot-scope="scope">
                              <div slot="reference" class="name-wrapper">
                                    <el-tag size="small"  type='success' v-if="scope.row.zt == ''">{{ scope.row.zt }}</el-tag>
                                    <el-tag size="small"  type='danger' v-if="scope.row.zt != ''">{{ scope.row.zt }}</el-tag>
                              </div>
                         </template>
                    </el-table-column>
                    <el-table-column prop="wglv" label="(%)" width="100" show-overflow-tooltip v-if="ishow"> </el-table-column>
                    <el-table-column prop="wglg" label="()" width="100" v-if="ishow"> </el-table-column>
                    <el-table-column prop="cnjssj" sortable label="" width="150"> </el-table-column>
                    <el-table-column prop="sjjssj" sortable label="" width="150"> </el-table-column>
                    <el-table-column prop="zzrxm"  label="" width="100"> </el-table-column>
                </el-table>
           </div>
           <div style="padding:5px 0;text-align:right">
                <pagination v-if="total > 12" :currentPage="currentPage" :pageSize="pageSize" :total="total" @handleCurrentChange="handleCurrentChange"></pagination>
           </div>     
       </section>

       <el-dialog
            title=""
            :visible.sync="planVisible"
            :close-on-click-modal="false"
            width="600px">
             <span style="color:#aaa;font-size:12px;padding-left:20px"></span>
            <el-form ref="form" :model="form" label-width="120px" style="padding:10px 20px 10px 0">
                <el-form-item label="">
                    <el-date-picker v-model="form.startDate" type="date" size="small" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd">
                     </el-date-picker>
                </el-form-item>
               
                 <el-form-item label="">
                    <el-date-picker v-model="form.endDate" type="date" size="small" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd">
                     </el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="form.sm" resize="none" :rows="5"></el-input>
                </el-form-item>
                <div style="text-align:right">
                    <el-button type="primary" size="small" @click="handleCommit"></el-button>
                    <el-button type="info" size="small" @click="handleClose"></el-button>
                </div>
            </el-form> 
        </el-dialog>
        <el-dialog
            title=""
            :close-on-click-modal="false"
            :visible.sync="milestoneVisible"
            width="600px"
            top="50px">
            <commitMilestone :shown="milestoneVisible"  @handleCommitMilestone="handleCommitMilestone" :xmbh="xmbh" :taskLcbbhArr="lcbbhArr"></commitMilestone>
        </el-dialog>
        
  </div>
</template>
<script>
import { queryMilestoneData ,submitMilestone ,ModifyMilestoneCommitmentDate,getMilestoneSubmitType } from '@/api/milestone.js'
import pagination from '@/components/BusinessPage/pagination.vue'
import commitMilestone from '@/components/BusinessPage/commitMilestone.vue'
import { returnFloat} from '../../utils/util.js'; 
export default {
  data(){
      return {
          checkList: [],
          xmlbList:[],
          keyword:'',
          cnkssj:"",
          cnjssj:"",
          sjkssj:"",
          sjjssj:"",
          total:null,
          pageSize:12,
          currentPage:1,
          tableData3: [],
          totalWgl:'',
          multipleSelection: [],
          planVisible:false,
          milestoneVisible:false,
          ishownTZJH:true,
          form:{
            startDate:"",
            endDate:"",
            sm:""
          },
          lcbbhArr:[],
          groupTag:'',
          ishow:true
      }
  },
  props:{
      xmbh:{
            type:String,
            default:""
      }
  },
   methods: {
        handleCommitMilestone(){
            this.milestoneVisible = false;
            this.queryMilestoneData(this.currentPage);
        },
        checkboxInit(row,index){
            if (row.zt != '' && row.zt != '') // && row.zt != ''
                return 0;
            else
                return 1;
            },
        handleClose(){  //
            this.planVisible = false
        },

       handleCommit(){ //
            ModifyMilestoneCommitmentDate({
            xmbh:this.xmbh,
            lcbbh:this.lcbbhArr.join(','),
            startDate:this.form.startDate,
            endDate:this.form.endDate,
            sm:this.form.sm
          }).then(({data})=>{
             if(data.state == 'success'){
                this.planVisible = !this.planVisible
                this.$alert('', '', {
                 confirmButtonText: '',
                 type:'success',
                 callback: action => {
                   this.form.sm = ''
                   this.queryMilestoneData(1);
                 }
              });  
             }
          })
       },
       // 
       commitLcb(){
         if(this.multipleSelection.length == 0){
              this.$alert('', '', {
                 confirmButtonText: '',
                 type:'warning',
              }); 
            return;
          }else{
              getMilestoneSubmitType({
                    lcbbh:this.lcbbhArr.join(',')
                }).then(({data})=>{
                    if(data.state == 'success'){
                        if(data.data.lcbType == -1){
                             this.$alert('','', {confirmButtonText: '',type:'warning',}); 
                        }else{
                            this.milestoneVisible = !this.milestoneVisible
                        }
                    }
                })
          }
       },
       exportLcb(){
           this.cnkssj = !this.cnkssj?'':this.cnkssj;
           this.cnjssj = !this.cnjssj?'':this.cnjssj;
           this.sjkssj = !this.sjkssj?'':this.sjkssj;
           this.sjjssj = !this.sjjssj?'':this.sjjssj;
          window.open(window.baseurl+'milestone/exportMilestoneData.do?xmbh='+this.xmbh+'&keyword='+this.keyword+'&zt='+ this.checkList.join(',')+
          '&startCnjssj='+this.cnkssj+'&endCnjssj='+this.cnjssj+'&startSjjssj='+this.sjkssj+'&endSjjssj='+this.sjjssj+'&nrxmlb='+this.xmlbList.join(','));
       },
        //     
    //   adjustmentPlan(){
    //       if(this.multipleSelection.length == 0){
    //           this.$alert('', '', {
    //              confirmButtonText: '',
    //              type:'warning',
    //           }); 
    //         return;
    //       }
    //       this.planVisible = !this.planVisible
    //   },
      handleSearchLcb(){  //
          this.queryMilestoneData(1);
      },
      searchLcbContent(){  //
         this.queryMilestoneData(1);
      },
      handleCurrentChange(data){   // 
         this.queryMilestoneData(data);
         this.currentPage = data
      },

      handleCheckbox(val){    //
        this.queryMilestoneData(1);
      },
      handleXMlb(val){   //
         this.queryMilestoneData(1);
      },
      changeCnStartDate(){   //
        this.queryMilestoneData(1);
      },
      changeCnEndDate(){   //
        this.queryMilestoneData(1); 
      },
      changeSjStartDate(){ //
        this.queryMilestoneData(1);
      },
      changeSjEndDate(){  //
        this.queryMilestoneData(1);
      },
      //   
      handleSelectionChange(val) {
        this.multipleSelection = val;
        this.lcbbhArr = [];
        if(this.multipleSelection.length != 0){
            this.multipleSelection.forEach((ele,i,arr)=>{
                if(ele.lcbms.includes('')){
                    this.ishownTZJH = false
                }else{
                    this.ishownTZJH = true 
                }
              this.lcbbhArr.push(ele.lcbbh);
            })
        }else{
            this.ishownTZJH = true  
        }
      },
      // 
     queryMilestoneData(curPage){
         queryMilestoneData({
            curPage:curPage,
            pageSize:this.pageSize,
            xmbh:this.xmbh,
            keyword:this.keyword,
            zt:this.checkList.length == 0?"":this.checkList.join(','),
            startCnjssj:!this.cnkssj?'':this.cnkssj,
            endCnjssj:!this.cnjssj?'':this.cnjssj,
            startSjjssj:!this.sjkssj?'':this.sjkssj,
            endSjjssj:!this.sjjssj?'':this.sjjssj,
            nrxmlb:this.xmlbList.length == 0?"":this.xmlbList.join(',')
         }).then(({data})=>{
            if(data.state == 'success'){
                this.totalWgl = data.data.totalWgl
                let  milestone = data.data.data.rows
                 milestone.forEach((val,index,arr)=>{
                    arr[index].wglv = returnFloat(val.wglv);
                    arr[index].wglg = returnFloat(val.wglg);
                   val.zt = val.zt == '1'?"":val.zt == '2'?"":val.zt == '3'?"":val.zt == '4'?"":val.zt == '5'?"":val.zt == '6'?"":val.zt == '7'?"":""
                });
                this.tableData3 = milestone
                this.total = data.data.data.records
            }
         })
     }
    },
    mounted(){
        this.groupTag = JSON.parse(sessionStorage.userInfo).userGroupTag;
        if(this.groupTag.indexOf('JZGCRY') != -1){
            this.ishow = true
        }else{
            this.ishow = false         
        }
        this.queryMilestoneData(1);
    },
    components:{pagination,commitMilestone}
}
</script>
<style scoped>
.milestone-manage{
    width: 90%;
    margin:0 auto;
    padding:8px 0 0 0;
}
.filter{
    margin-bottom: 15px;
}
.filter-title{
    display: inline-block;
    width: 100px;
    text-align: right;
    font-weight: 700;
    margin-right:10px;
}
.milestone_tabel{
    margin:10px 0;
    border-radius:5px;
    padding-top:3px;
    background:#fff;
    box-shadow:0 0 5px #ccc
}

</style>



