<template>
 <div class="item-addUser-xmtd">
   <div class="item-addUser-xmtd--addbtn">
     <el-button size="small" type="primary" icon="el-icon-circle-plus-outline" @click="handleAddcy"></el-button>
   </div>
   <div class="item-addUser-outtable">
      <el-table :data="xmtdList" style="width: 100%" border>
        <el-table-column
            fixed="left"
            label=""
            width="120">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="info" size="mini" plain></el-button>
              <el-button @click="handleDeleteClick(scope.$index,scope.row,xmtdList)" type="danger" size="mini" plain ></el-button>
            </template>
        </el-table-column>
        <el-table-column prop="cymc" label="" width="150"></el-table-column>
        <el-table-column prop="fbmc" label="" width="250" show-overflow-tooltip></el-table-column>
        <el-table-column prop="cpmc" label="" width="150" show-overflow-tooltip></el-table-column>
        <el-table-column label="" width="110">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{!scope.row.sfcpfzr?'':''}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="jhfpfy" label="" width="110" show-overflow-tooltip></el-table-column>
        <el-table-column prop="ydgznr" label=""  show-overflow-tooltip></el-table-column>
        <el-table-column prop="ydkssj" label="" width="110"></el-table-column>
        <el-table-column prop="ydjssj" label="" width="110"></el-table-column>
        <!-- <el-table-column prop="fbbh" label="" width="130"></el-table-column> -->
        <!-- <el-table-column prop="cpbh" label="" width="130"></el-table-column> -->
      </el-table>
    </div>

    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :close-on-click-modal='false'
      width="750px">
      <div class="item-addUser-xmtd--dialog">
        <div>
           <el-input v-model="keyword" size="mini" placeholder="//" @change="handleQueryuser"></el-input>
        </div>
          <ul class="item-addUser-xmtd--userList">
              <li>
                <span></span>
                <span>/</span>
                <span></span>
                <span></span>
              </li>
              <li v-for="(user,i) in userList">
                <span>{{user.username}}</span>
                <span>{{user.usercode}}</span>
                <span>{{user.email}}</span>
                <span><el-button size="mini" @click="handleChooseUser(user)"></el-button></span>
              </li>
          </ul>
           <div v-if="total > 12" style="text-align:center">
             <pagination :pageSize="12" :total="total" @handleCurrentChange="handleCurrentChange"></pagination>
           </div>
      </div>
    </el-dialog>
    <el-dialog
        width="750px"
        title=""
        :visible.sync="innerVisible"
        :close-on-click-modal='false'
        append-to-body>
        <el-form ref="form" :model="tdcyInfo" label-width="110px" size="mini" :inline="true" style="padding:5px 10px">
            <el-form-item label="" required >
              <el-select v-model="tdcyInfo.fbbh" placeholder="" style="width:510px">
                <el-option v-for="(fb,i) in fbxxList" :key="i" :label="fb.id+' '+fb.text" :value="fb.id"></el-option>
              </el-select>
            </el-form-item>
            <br>
            <el-form-item label="" required>
              <el-input v-model="tdcyInfo.cymc" readonly style="width:510px"></el-input>
            </el-form-item>
            <el-form-item label="" required>
              <el-select v-model="tdcyInfo.cp" placeholder="" style="width:510px">
                <el-option v-for="(cp,i) in xmcpList" :key="i" :label="cp.cp" :value="cp.cpbh+'&'+cp.cp"></el-option>
              </el-select>
            </el-form-item><br>
            <el-form-item label="" required>
              <el-select v-model="cpxdata" multiple placeholder=""  style="width:428px" :disabled="isAllCpx">
                <el-option v-for="(cpx,i) in cpxData" :key="i" :label="cpx.mc" :value="cpx.mc+','+cpx.label"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" >
                <el-checkbox v-model="isAllCpx"></el-checkbox>
            </el-form-item>
            <el-form-item label="" required>
              <el-date-picker type="date" placeholder="" v-model="tdcyInfo.ydkssj" value-format="yyyy-MM-dd" style="width:193px"></el-date-picker>
            </el-form-item>
            <el-form-item label="" required>
              <el-date-picker type="date" placeholder="" v-model="tdcyInfo.ydjssj" value-format="yyyy-MM-dd" style="width:193px"></el-date-picker>
            </el-form-item>
            <el-form-item label="" required>
              <el-input v-model="tdcyInfo.jhfpfy" style="width:193px"></el-input>
            </el-form-item>
             <el-form-item label="" >
               <el-radio-group v-model="sfcpfzr" :disabled='iscpzrr && wid != ""'>
                    <el-radio :label="true"></el-radio>
                    <el-radio :label="false"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" v-model="tdcyInfo.ydgznr" :rows="5" :cols="88"></el-input>
            </el-form-item><br>
          </el-form>
            <div style="text-align:right;padding:8px 20px;">
                  <el-button type="primary" @click="handleSaveinfo" size="mini"></el-button>
            </div>
      </el-dialog>    

 </div>
</template>

<script>
import { getTeamMembers ,getProjectCatalog,getFbxxSelect,getUsers,deleteTeamMember,saveTeamMember} from '@/api/xmtd.js'
import { getRyCpx} from '@/api/personal.js'
import pagination from '@/components/BusinessPage/pagination.vue'
import { getMenu,getSession} from '@/utils/util.js'
 export default {
   data () {
     return {
           dialogVisible:false,
           innerVisible:false,
           xmtdList:[],
           thList:['','','','','','','',''],
           xmcpList:[],
           fbxxList:[],
           userList:[],
           keyword:"",
           total:null,
           cpxData:[],
           tdcyInfo:{
             fbbh:"",
             cymc:"",
             cybh:"",
             cp:"",
             jhfpfy:"",
             ydkssj:"",
             ydjssj:"",
             ydgznr:"",
           },
            sfcpfzr:true,
            userId:'',
            cpxdata:[],
            wid:"",
            fzywList:[],
            iscpzrr:false,
            isAllCpx:''
       }
   },
   props:{
      tabName:{
        type:String,
        default:''
      },
      xmbh:{
         type:String,
         default:''
      }
   },
   mounted(){
      
   },
   methods:{
        handleSaveinfo(){  //
           if(!this.validate()) return;
           if(this.wid){
              this.saveTeamMember(this.wid);
           }else{
              this.saveTeamMember('');
           }
        },
        handleAddcy(){
            this.getUsers(1,0);
            this.dialogVisible = !this.dialogVisible;
        },
        handleQueryuser(){ // 
            this.getUsers(1,0);
        },
        handleCurrentChange(data){  // 
            this.getUsers(data,0);
        },

        handleClick(data){ // 
            this.cpxdata = [];
            this.wid = data.wid
            this.tdcyInfo.fbbh = data.fbbh;
            this.tdcyInfo.cymc = data.cymc;
            this.tdcyInfo.cybh = data.cybh;
            this.tdcyInfo.cp =  !data.cpbh?'':data.cpbh+'&'+data.cpmc;
            this.tdcyInfo.ydkssj = data.ydkssj;
            this.tdcyInfo.ydjssj = data.ydjssj;
            this.tdcyInfo.jhfpfy = data.jhfpfy;
            this.tdcyInfo.ydgznr = data.ydgznr;
            this.sfcpfzr = data.sfcpfzr
            this.iscpzrr = data.sfcpfzr
            this.userId = data.userId
            getRyCpx({  //  
              yhbh:data.userId,
              xmbh:this.xmbh
            }).then(({data})=>{
                if(data.state=='success'){
                  this.isAllCpx = data.data.isAllCpx
                  if(data.data.isAllCpx){
                    this.cpxdata = [];
                  }else{
                    if(data.data.cpx && data.data.cpx.length !=0){
                        data.data.cpx.forEach((ele,i,arr)=>{
                        this.cpxdata.push(ele.cpxmc+','+ele.cpxbh);
                     })
                    }else{
                        this.cpxdata = [];
                    }
                  }
                }
            })
            this.innerVisible = true
        },
        handleDeleteClick(index,data,rows){ // 
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            deleteTeamMember({wid:data.wid}).then(({data})=>{
                if(data.state == 'success'){
                  this.$alert('', '', {confirmButtonText: '', type:'success',callback: action => {
                    rows.splice(index, 1);
                  }});   
                }else{
                  this.$alert(data.msg, '', {confirmButtonText: '', type:'error',callback: action => {}});   
                }
            })
          }).catch(() => {});
        },  
        handleChooseUser(data){  // 
            this.tdcyInfo = {};
            this.cpxdata = [];
            this.wid = '';
            this.iscpzrr =  false; // 
            this.isAllCpx = true; // 
            this.tdcyInfo.cymc = data.username
            this.tdcyInfo.cybh = data.usercode
            this.userId = data.userid
            this.innerVisible = !this.innerVisible;
        },
        getTeamMembers(){//
            getTeamMembers({
              xmbh:this.xmbh
            }).then(({data})=>{ 
                if(data.state == 'success'){
                  if(data.data && data.data.length != 0){
                    this.xmtdList = data.data
                  }else{
                    this.xmtdList = []
                  }
                }else{
                   this.$alert(data.msg, '', {confirmButtonText: '', type:'error',callback: action => {}});   
                }
            })
        },
        getUsers(curPage,unitType){ //
            getUsers({
              curPage:curPage,
              pageSize:12,
              unitType:unitType,
              keyword:this.keyword}).then(({data})=>{
                if(data.state == 'success'){
                  this.userList = data.data.rows
                  this.total = data.data.records
                }else{
                   this.$alert(data.msg, '', {confirmButtonText: '', type:'error',callback: action => {}});   
                }
              })
        },
        saveTeamMember(wid){
          saveTeamMember({
              wid:wid,
              xmbh:this.xmbh,
              cybh:this.tdcyInfo.cybh,
              userId:this.userId,
              cymc:this.tdcyInfo.cymc,
              cpmc:this.tdcyInfo.cp.split('&')[1],
              cpbh:this.tdcyInfo.cp.split('&')[0],
              fbbh:this.tdcyInfo.fbbh,
              ydkssj:this.tdcyInfo.ydkssj,
              ydjssj:this.tdcyInfo.ydjssj,
              jhfpfy:this.tdcyInfo.jhfpfy,
              ydgznr:this.tdcyInfo.ydgznr,
              sfcpfzr:this.sfcpfzr,
              cpxdata:this.isAllCpx?'':this.cpxdata.join('|'),
              isAllCpx:this.isAllCpx
          }).then(({data})=>{
              if(data.state == 'success'){
                this.innerVisible = false;
                 this.$alert('', '', {
                  confirmButtonText: '',
                  type:"success",
                  callback: action => {
                       this.getTeamMembers();  
                  }
                });
              }else{
                  this.$alert(data.msg, '', {confirmButtonText: '', type:'error',callback: action => {}});   
              }
          })
        },

       validate(){
         if(!this.tdcyInfo.fbbh){
           this.$alert('', '', {confirmButtonText: '', type:'warning',callback: action => {}});
           return false;
         }else if(!this.tdcyInfo.cp){
           this.$alert('', '', {confirmButtonText: '', type:'warning',callback: action => {}});
           return false;
         }else if(!this.isAllCpx && this.cpxdata.length == 0){
           this.$alert('', '', {confirmButtonText: '', type:'warning',callback: action => {}});
           return false;
         }else if(!/^\d+(\.\d+)?$/.test(this.tdcyInfo.jhfpfy)){
           this.$alert('', '', {confirmButtonText: '', type:'warning',callback: action => {}});
           return false;
         }else if(!this.tdcyInfo.ydkssj){
           this.$alert('', '', {confirmButtonText: '', type:'warning',callback: action => {}});
           return false;
         }else if(!this.tdcyInfo.ydjssj){
           this.$alert('', '', {confirmButtonText: '', type:'warning',callback: action => {}});
           return false;
         }else{
           return true;
         }
       }
   },
   watch:{
      tabName(n,o){
        if(n == 'second'){
            this.getTeamMembers();
            if(!getSession('cp')){
                getMenu('cp',this.cpxData,true)  //
            }else{
                this.cpxData = getSession('cp');
            }
            if(this.xmcpList.length == 0){
               getProjectCatalog({xmbh:this.xmbh}).then(({data})=>{ // 
               if(data.state == 'success'){
                 this.xmcpList = data.data
               }    
              }) 
            }
            if(this.fbxxList.length == 0){
              getFbxxSelect({xmbh:this.xmbh}).then(({data})=>{ // 
                if(data.state == 'success'){
                  this.fbxxList = data.data
                } 
              })
            }  
         }
      }
   },
   components: {pagination}
 }
</script>

<style scoped>
.item-addUser-xmtd-xmtd {
  margin: 0 20px;
  width: 70vw;
}
.item-addUser-xmtd--addbtn{
  text-align: right;
  padding-bottom:10px;
}
.item-addUser-outtable{
  max-height: 65vh;
  overflow-y: auto;
}
.item-addUser-outtable button{
  padding: 5px 8px;
}
.item-addUser-xmtd--dialog div{
   margin: 5px 0;
   padding: 0 6px;
}
.item-addUser-xmtd--userList li{
  display: flex;
  padding: 5px;
  border-bottom: 1px solid #ccc;
}
.item-addUser-xmtd--userList li:hover{
  background: #ecf5ff;
}
.item-addUser-xmtd--userList li:nth-of-type(1){
  background: #dbdddf;
  font-weight: 700;
}
.item-addUser-xmtd--userList li span{
  display: inline-block;
   width: 25%;
   text-align: center;
}
</style>



